export const formatPrice = (number) => {
   return new Intl.NumberFormat('en-US', {
     style: 'currency',
     currency: 'USD',
   }).format(number / 100)
 }





export const getUniqueItem = (data,type) =>
{
   let unique= data.map((item)=>item[type])
   if (type === 'category') {
      unique = unique.flat()
  } else if(type === 'company')
   {
      unique = unique.flat()
   }
   else if(type === 'colors')
         {
          unique = unique.flat()
         }

  return ['all',...new Set(unique)]
}